___CSS_LOADER_EXPORT___.push([module.id, `.label {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  color: #ffffff; /* Text color for dark theme */
}

.label-text {
  margin-left: 16px;
}

.toggle {
  isolation: isolate;
  position: relative;
  height: 20px;
  width: 40px;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: -8px -4px 8px 0px #1a2026, 8px 4px 12px 0px #394a56, 4px 4px 4px 0px #394a56 inset, -4px -4px 4px 0px #1a2026 inset;
}

.toggle-state {
  display: none;
}

.indicator {
  height: 100%;
  width: 200%;
  background: #394a56; /* Background color for dark theme */
  border-radius: 15px;
  transform: translate3d(-75%, 0, 0);
  transition: transform 0.4s cubic-bezier(0.85, 0.05, 0.18, 1.35);
  box-shadow: -8px -4px 8px 0px #1a2026, 8px 4px 12px 0px #394a56;
}

.toggle-state:checked ~ .indicator {
  transform: translate3d(25%, 0, 0);
}`, "",{"version":3,"sources":["webpack://./src/components/Toggle.css"],"names":[],"mappings":"AAAA;EACE,oBAAA;EACA,mBAAA;EACA,eAAA;EACA,cAAA,EAAA,8BAAA;AACF;;AAEA;EACE,iBAAA;AACF;;AAEA;EACE,kBAAA;EACA,kBAAA;EACA,YAAA;EACA,WAAA;EACA,mBAAA;EACA,gBAAA;EACA,+HAAA;AACF;;AAGA;EACE,aAAA;AAAF;;AAGA;EACE,YAAA;EACA,WAAA;EACA,mBAAA,EAAA,oCAAA;EACA,mBAAA;EACA,kCAAA;EACA,+DAAA;EACA,+DAAA;AAAF;;AAGA;EACE,iCAAA;AAAF","sourcesContent":[".label {\r\n  display: inline-flex;\r\n  align-items: center;\r\n  cursor: pointer;\r\n  color: #ffffff; /* Text color for dark theme */\r\n}\r\n\r\n.label-text {\r\n  margin-left: 16px;\r\n}\r\n\r\n.toggle {\r\n  isolation: isolate;\r\n  position: relative;\r\n  height: 20px;\r\n  width: 40px;\r\n  border-radius: 15px;\r\n  overflow: hidden;\r\n  box-shadow: -8px -4px 8px 0px #1a2026, 8px 4px 12px 0px #394a56,\r\n    4px 4px 4px 0px #394a56 inset, -4px -4px 4px 0px #1a2026 inset;\r\n}\r\n\r\n.toggle-state {\r\n  display: none;\r\n}\r\n\r\n.indicator {\r\n  height: 100%;\r\n  width: 200%;\r\n  background: #394a56; /* Background color for dark theme */\r\n  border-radius: 15px;\r\n  transform: translate3d(-75%, 0, 0);\r\n  transition: transform 0.4s cubic-bezier(0.85, 0.05, 0.18, 1.35);\r\n  box-shadow: -8px -4px 8px 0px #1a2026, 8px 4px 12px 0px #394a56;\r\n}\r\n\r\n.toggle-state:checked ~ .indicator {\r\n  transform: translate3d(25%, 0, 0);\r\n}\r\n"],"sourceRoot":""}]);
