.drag-drop-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-radius: 10px;
  box-shadow: var(--shadow-theme);
  transition: box-shadow 0.3s ease;
}

.drag-drop-container:hover {
  box-shadow: var(--shadow-theme-hover);
}

.input-container-file {
  position: relative;
  overflow: hidden;
  width: 100%;
  min-height: 15px;
  border: 2px dashed #ccc; /* Added border style */
  border-radius: 8px; /* Added border radius */
  padding: 10px; /* Added padding to visually separate the border */
  text-align: center;
}

.input-container-file input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

.preview-panel-files {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 20px;
}

.preview-panel-files img {
  width: 100px; /* Adjust the width as needed */
  height: 100px; /* Adjust the height as needed */
  object-fit: cover;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Add Neumorphic styles to the preview button */
.file-prev-container {
  font-size: 10px;
}
.file-prev-container .btn-remove {
  position: relative;
  left: 30px;
  top: 34px;
  height: 20px;
  z-index: 999;
}
.file-prev-container img {
  width: 40px;
  height: 40px;
}
.file-prev-container .default-file-icon {
  font-size: 40px;
}
.file-prev-container .file-name {
  display: inline-block;
  max-width: 50px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  position: relative;
}
.file-prev-container .file-name::after {
  content: attr(data-tooltip);
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  white-space: normal;
  background-color: #333;
  color: #fff;
  padding: 0.5em;
  border-radius: 0.25em;
  display: none;
}`, "",{"version":3,"sources":["webpack://./src/components/Files/Files.scss"],"names":[],"mappings":"AAAA,eAAA;AAEA;EACE,aAAA;EACA,sBAAA;EACA,mBAAA;EACA,aAAA;EACA,mBAAA;EACA,+BAAA;EACA,gCAAA;AAAF;;AAGA;EACE,qCAAA;AAAF;;AAGA;EACE,kBAAA;EACA,gBAAA;EACA,WAAA;EACA,gBAAA;EACA,uBAAA,EAAA,uBAAA;EACA,kBAAA,EAAA,wBAAA;EACA,aAAA,EAAA,kDAAA;EACA,kBAAA;AAAF;;AAGA;EACE,kBAAA;EACA,MAAA;EACA,OAAA;EACA,WAAA;EACA,YAAA;EACA,UAAA;EACA,eAAA;AAAF;;AAGA;EACE,aAAA;EACA,uBAAA;EACA,mBAAA;EACA,eAAA;EACA,SAAA;EACA,gBAAA;AAAF;;AAGA;EACE,YAAA,EAAA,+BAAA;EACA,aAAA,EAAA,gCAAA;EACA,iBAAA;EACA,kBAAA;EACA,wCAAA;AAAF;;AAGA,gDAAA;AAEA;EACE,eAAA;AADF;AAEE;EACE,kBAAA;EACA,UAAA;EACA,SAAA;EACA,YAAA;EACA,YAAA;AAAJ;AAEE;EACE,WAAA;EACA,YAAA;AAAJ;AAEE;EACE,eAAA;AAAJ;AAEE;EACE,qBAAA;EACA,eAAA;EACA,gBAAA;EACA,mBAAA;EACA,uBAAA;EACA,kBAAA;AAAJ;AAEE;EACE,2BAAA;EACA,kBAAA;EACA,SAAA;EACA,SAAA;EACA,2BAAA;EACA,mBAAA;EACA,sBAAA;EACA,WAAA;EACA,cAAA;EACA,qBAAA;EACA,aAAA;AAAJ","sourcesContent":["/* Files.scss */\r\n\r\n.drag-drop-container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  padding: 20px;\r\n  border-radius: 10px;\r\n  box-shadow: var(--shadow-theme);\r\n  transition: box-shadow 0.3s ease;\r\n}\r\n\r\n.drag-drop-container:hover {\r\n  box-shadow: var(--shadow-theme-hover);\r\n}\r\n\r\n.input-container-file {\r\n  position: relative;\r\n  overflow: hidden;\r\n  width: 100%;\r\n  min-height: 15px;\r\n  border: 2px dashed #ccc; /* Added border style */\r\n  border-radius: 8px; /* Added border radius */\r\n  padding: 10px; /* Added padding to visually separate the border */\r\n  text-align: center;\r\n}\r\n\r\n.input-container-file input {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  opacity: 0;\r\n  cursor: pointer;\r\n}\r\n\r\n.preview-panel-files {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  flex-wrap: wrap;\r\n  gap: 10px;\r\n  margin-top: 20px;\r\n}\r\n\r\n.preview-panel-files img {\r\n  wi